import * as React from 'react';

import {
  CopyBetaNormal,
  DeleteBetaNormal,
  DeleteLinkNormal,
  EditorBorderBetaNormal,
  Tooltip,
} from '@ali/we-design';

import { ToolbarIconWrapper } from './styled';

interface ToolbarIconProps {
  type: string;
  tip?: string;
  onMouseDown: (event?: React.MouseEvent) => void;
}

const ToolbarIcon: React.FC<ToolbarIconProps> = (props) => {
  const { onMouseDown, type, tip, ...rest } = props;

  let icon: React.ReactElement;
  switch (type) {
    case 'editor-border-beta':
      icon = <EditorBorderBetaNormal />;
      break;
    case 'copy-beta':
      icon = <CopyBetaNormal />;
      break;
    case 'delete-beta':
      icon = <DeleteBetaNormal />;
      break;
    case 'delete-link-beta':
      icon = <DeleteLinkNormal />;
      break;
    default:
      break;
  }
  return (
    <Tooltip title={tip} placement="bottom">
      <ToolbarIconWrapper onMouseDown={onMouseDown} {...rest}>
        {icon}
      </ToolbarIconWrapper>
    </Tooltip>
  );
};

export default React.memo(ToolbarIcon);
